<template>
  <div>
    <div class="card_info_item" v-for="item in list">
      <div class="card_info_item_title">
        {{ item.title }}
      </div>
      <div class="card_info_item_content">
        {{ item.content }}
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "FormList",
  props: ["list"]
};
</script>
<style>
.card_info_item {
  background: white;
  padding: 8px;
  display: flex;
  margin-bottom: 8px;
}
.card_info_item_title {
  flex-shrink: 0;
  min-width: 30%;
  font-weight: 500;
}
.card_info_item_content {
  flex-grow: 1;
  text-align: right;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
  text-wrap-mode: nowrap;
}
</style>



