<template>
  <div class="data_body">
    <Header
      :dataset_name="dataset_name"
      :dataset_icon="dataset_icon"
      :keys="keys"
      :dataset_viewer="dataset_viewer"
    />
    <v-row>
      <v-col cols="4">
        <Card title="简介" :content="discription" />
        <Card title="发布信息">
          <FormList :list="fromInfo" />
        </Card>
        <Card title="引用">
          <CiteInfo :info="citeInfo" />
        </Card>
      </v-col>
      <v-col cols="8">
        <Card title="数据集性质">
          <ColorCard :info="useInfo" :splitCol="4" />
        </Card>
        <Card title="数据信息">
          <TableLine v-if="tableData" :tableData="tableData" />
          <HightlightLine v-if="data_colums" :list="data_colums" />
        </Card>
        <Card title="数据集划分">
          <DownloadCard :list="datasets_split_info" />
        </Card>
        <Card title="使用方法">
          <Code :code="code" />
        </Card>
      </v-col>
    </v-row>
    <Footer />
  </div>
</template>
<script>
import PrismEditor from "../editor";
import Header from "../Header.vue";
import Card from "../Card.vue";
import FormList from "../FormList.vue";
import CiteInfo from "../CiteInfo.vue";
import ColorCard from "../ColorCard.vue";
import TableLine from "../TableLine.vue";
import HightlightLine from "../HightlightLine.vue";
import DownloadCard from "../DownloadCard.vue";
import Code from "../Code.vue";
import Footer from "../Footer.vue";
export default {
  name: "AppList",
  components: {
    PrismEditor,
    Header,
    Card,
    CiteInfo,
    FormList,
    ColorCard,
    TableLine,
    HightlightLine,
    DownloadCard,
    Code,
    Footer
  },
  props: [
    "dataset_name",
    "dataset_icon",
    "discription",
    "dataset_viewer",
    "keys",
    "fromInfo",
    "citeInfo",
    "code",
    "useInfo",
    "datasets_split_info",
    "tableData",
    "data_colums"
  ]
};
</script>
<style></style>



